import { Component } from '@angular/core';
import {smoothlyMenu} from "../../app.helpers";
import  {topnavbarService} from "./topnavbar.service";
import { HttpClient } from '@angular/common/http';

@Component({
    selector: 'topnavbar',
    templateUrl: 'topnavbar.component.html'
})
export class Topnavbar {
    loggedInUser: any;
    constructor( private topnavbarService: topnavbarService,private http: HttpClient) {
        this.http.get('/app/token')
        .subscribe((response: any) => {
            console.log("shilpa :",JSON.stringify(response))
            this.loggedInUser = response.user
        })
    }
    ngOnInit() {

    }
    toggleNavigation(): void {
        jQuery("body").toggleClass("mini-navbar");
        smoothlyMenu();
    }
    logout() {
        localStorage.clear();
        this.topnavbarService.updateUser(this.loggedInUser)
        // location.href='http://to_login_page';
    }
}


