import {OnInit, Component} from "@angular/core";
import * as io from 'socket.io-client';
import { HttpClient } from '@angular/common/http';
import  {topnavbarService} from "../../components/topnavbar/topnavbar.service";

@Component({
    selector: 'home',
    templateUrl: './home.component.html',
    styleUrls: [
        './home.component.scss',
    ]
})
export class HomeComponent implements OnInit {
    getDisUser: any;
    loggedUser: any;
    newMessage: any;
    socket: SocketIOClient.Socket;
    chatList;
    messages;
    constructor( private http: HttpClient,private topnavbarService: topnavbarService) {
        this.chatList = [];
        this.messages = [];
        this.topnavbarService.lastDisconnected.subscribe(
            (lang) => {
              this.getDisUser = this.topnavbarService.getUser();
              console.log("message :", this.getDisUser);
              this.socket.on('disconnect', this.getDisUser)

            }
          );
        this.http.get('/app/token')
        .subscribe((response: any) => {
            this.loggedUser = response.user 
        let soptions = {
            reconnection: true,
            reconnectionDelay: 3000,
            reconnectionDelayMax: 5000,
            reconnectionAttempts: Infinity
          }
          this.socket = io('http://localhost:3006/', soptions);
          this.socket.connect();
          this.socket.emit('adduser', response.user, 'create', 'true');
          this.socket.on('new message', (msg: any) => {
              if(!msg.chatType && msg.status){
                  let indx =  this.chatList.filter(obj => obj.uname == msg.uname);
                       console.log("index: ",indx,msg,this.loggedUser)
                  if(msg.uname != this.loggedUser && indx.length == 0 ){
                    this.chatList.push(msg)
                  }
              }
              else if(msg.chatType){
                this.messages.push(msg)
              }else if(!msg.status){
                  console.log("popop disconnected",msg)
                  for(var i=0; i<this.chatList.length; i++){
                    if(this.chatList[i].uname == msg.uname){
                        this.chatList.splice(i, 1);  //removes 1 element at position i 
                        break;
                    }
                }
              }
        console.log("message :",msg)  
        })
    })
    

    }

    ngOnInit() {
    }
    sendNewMessage() {
       
        let obj = {
          message: this.newMessage,
          uname: this.loggedUser,
          date: new Date(),
          gname: 'create',
          type :'message',
          chatType:true,
          status:true
        }
        this.socket.emit('sendmessage', obj);
        this.newMessage = '';
      }
}


